.App {
  text-align: center;
  position: relative;
  width: 100%;
  height: 100vh;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #fcf0e5;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(7px + 2vmin);
  color: saddlebrown;
}
.App-footer {
  bottom: 0;
  left: 25%;
  max-width: 100%;
  max-height: 100%;
}
@media screen and (orientation: landscape) {
  .App-footer {
    width: 60%;
  }
}

@media screen and (orientation: portrait) {
  .App-footer {
    width: 100%;
  }
}
.dog {
  position: absolute;
  bottom: 0;
  left: 35%;
  max-width: 30%;
}

.App-top {
  animation: App-logo-spin infinite 20s linear;
  position:absolute;
  top:5%;
  left:5%;
  width: 20%;
}
.App-link {
  color: #61dafb;
}
.context {
  float: right;
  margin-top: 0.2rem;
  padding: 0.2rem 0.5rem;
  border: 0; border-radius: 1rem;
  height: 1.8rem; line-height: 1rem;
  font-size: 1rem;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
